import React, { useState } from 'react';
import { Activity } from '../activity/query';
import { Row, Col, Container, Modal, ModalHeader, ModalBody } from 'reactstrap';
import { Section, SectionType } from '../layout';
import { Truncate } from '../text';
import { formateDate } from '../text/formating';
import { getPublisher } from './util';

export default ({ activity, publishers }: { activity: Activity, publishers: NewsPublisher[] }) => {
    return (
        <div className='py-4 border-bottom'>
            <h6 className="text-center text-uppercase">{activity.title}</h6>
            <Row className='justify-content-center'>
                {activity.press_coverages.map((press, idx) => (
                    <Coverage
                        press={press}
                        key={idx}
                        activity={activity}
                        publisher={getPublisher(press.news_publisher ?? 0, publishers)}
                    />
                ))}
            </Row>
        </div>
    );
}

type CoverageProps = {
    press: PressCoverage
    activity: Activity
    publisher: NewsPublisher
}

const Coverage = ({ press, activity, publisher }: CoverageProps) => {

    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);

    return (
        <Col md="3">
            <a
                href="#"
                className="img-thumbnail d-block p-2 m-2"
                style={{
                    height: "150px",
                    overflow: 'hidden'
                }}
                onClick={toggle}
            >
                <img
                    className="img-fluid"
                    src={press.prints[0].localFile.publicURL}

                />
            </a>
            <Modal isOpen={isOpen} toggle={toggle} size='lg'>
                <ModalHeader toggle={toggle}>
                    <Truncate
                        text={activity.title}
                        max={50}
                        expandable={false}
                    />
                </ModalHeader>
                <ModalBody>
                    <img
                        src={press.prints[0].localFile.publicURL}
                        className='img-fluid d-block mx-auto'
                    />
                    <Row className='mt-3 border-top pt-3'>
                        <Col><strong>Event Title</strong></Col>
                        <Col>{activity.title}</Col>
                    </Row>
                    <Row className='mt-3'>
                        <Col><strong>Event Date</strong></Col>
                        <Col>{formateDate(activity.date)}</Col>
                    </Row>
                    <Row className='mt-3'>
                        <Col><strong>News Publisher</strong></Col>
                        <Col>
                            {publisher.website ?
                                <a href={publisher.website} target='_blank'>{publisher.name}</a>
                                : publisher.name
                            }
                        </Col>
                    </Row>
                    <Row className='mt-3'>
                        <Col><strong>Story Link</strong></Col>
                        <Col>
                            {press.link ?
                                <a href={press.link} target='_blank'>{press.link}</a>
                                : 'N/A'
                            }
                        </Col>
                    </Row>
                </ModalBody>
            </Modal>
        </Col>

    );
}